.login {
  background-color: white;
  height: calc(100vh - 262px); }
  .login .login-wrapper {
    margin: calc((100vh - 500px) / 4) 0; }
    .login .login-wrapper h1 {
      margin-top: 20px;
      width: 100%;
      text-align: center; }
    .login .login-wrapper .register {
      width: 100%;
      text-align: center;
      font-size: 1em; }
      .login .login-wrapper .register a {
        font-style: italic;
        text-decoration: none; }
    .login .login-wrapper .content {
      padding-top: 45px;
      width: 300px;
      height: auto;
      margin: auto; }
      .login .login-wrapper .content .password {
        margin: 25px 0px; }
      .login .login-wrapper .content .forgot-password {
        width: 100%;
        padding-left: 15px; }
        .login .login-wrapper .content .forgot-password a {
          font-style: italic;
          text-decoration: none; }
      .login .login-wrapper .content input {
        border-color: #888; }
    .login .login-wrapper .action {
      padding-top: 20px;
      width: 100%;
      text-align: center; }
      .login .login-wrapper .action button {
        padding: 0 25px; }



